.theme-mixin(@colorName, @color, @colorHex) {
    //Buttons
    .theme-@{colorName} .button:not(.button-fill) {
        border-color: @color;
        color: @color;
        html:not(.watch-active-state) &:active, &.active-state {
            background-color: rgba(red(@color), green(@color), blue(@color), 0.15);
        }
        &.active {
            background-color: @color;
            color:#fff;
        }
    }
    .theme-@{colorName} .button.button-fill {
        background: @color;
        color:#fff;
        html:not(.watch-active-state) &:active, &.active-state {
            background: @color;
        }
    }
    .theme-@{colorName} .subnavbar.no-border .button.tab-link {
      color: @color;
      background: #fff;
      &.active {
          &::after{
              background-color: @color;
          }
      }
    }
    // Progress bar
    .progressbar {
        &.theme-@{colorName}, .theme-@{colorName} & {
            span {
                background-color: @color;
            }
        }
    }
    .progressbar-infinite {
        &.theme-@{colorName}, .theme-@{colorName} & {
            &:before {
                background-color: @color;
            }
        }
    }
    //Icons
    .theme-@{colorName} {
        .icon, .icon& {
